import React from 'react'
import Price from '../Price'
function Conditions() {
  return (
    <div className='container conditions'>
        <header><h3>Opsti uslovi poslovanja</h3></header>
        <div className='title'>
            <h3>OPTI USLOVI POSLOVANjA POSREDNIKA U PROMETU I ZAKUPU NEPOKRETNOSTI</h3>
        </div>
        <div className='subtitle'>
            <h5>I UVODNE ODREDBE</h5>
        </div>
        <p>Optim uslovima poslovanja ureuje se poslovni odnos izmeu preduzetnika, kao
        posrednika u prometu i zakupu nepokretnosti i nalogodavca (fizikog ili pravnog lica).
        Zakljuenjem ugovora o posredovanju, odnosno prihvatanjem ovih uslova nalogodavac
        potvruje da je upoznat i saglasan sa odredbama Optih uslova poslovanja posrednika u prometu
        nepokretnosti.<br></br>
        Posredovanje se vri na osnovu Ugovora o posredovanju koji zakljuuju nalogodavac i
        posrednik.<br></br>
        Nalogodavac moe imati zakljuene Ugovore o posredovanju sa vie posrednika
        istovremeno, osim u sluaju ekskluzivnog posredovanja.<br></br>
        Kupac ili zakupac u svojstvu nalogodavca je obavezan da posredniki naknadu isplati
        posredniku koji mu je prvi omoguio razgledanje predmetne nepokretnosti, odnosno koji ga je prvi
        upoznao sa predmetnom nepokretnou. U sluaju da je nalogodavac (kupac ili zakupac) razgledao
        predmetnu nepokretnost, posredstvom drugog posrednika ili sam, pre nego to mu je posrednik istu
        pokazao, obavezan je da o tom upozna posrednika, a u sluaju da to ne uini, smatrae se da je
        nepokretnost prvi put razgledao posredstvom posrednika.<br></br>
        Kupac ili zakupac u svojstvu nalogodavca je obavezan da neposredno pre razgledanja
        nepokretnosti potpie posredniku Potvrdu o gledanju nepokretnosti, u kojoj potvruje da je
        predmetnu nepokretnost gledao posredstvom posrednika.<br></br>
        U sluaju da kupac ili zakupac odbije da potpie Potvrdu iz prethodnog stava, posrednik nije
        obavezan da mu predmetnu nepokretnost pokae. U sluaju da je Posrednik pokazao predmetnu
        nepokretnost kupcu ili zakupcu i pored odbijanja da potpie Potvrdu o gledanju, pa kupac ili
        zakupac ili sa njim povezana lica, zakljui Ugovor o prometu ili zakupu predmetne nepokretnosti,
        injenica da je kupca/zakupca u vezu sa prodavcem ili zakupodavcem doveo posrednik e se
        dokazivati drugim dokaznim sredstvima  svedocima, pisanom ili elektronskom dokumentacijom,
        sms porukama i slino.<br></br>
        Prodavac ili zakupodavac u svojstvu nalogodavca je obavezan da posredniku naknadu
        isplati posredniku koji ga je prvi doveo u vezu sa potencijalnim kupcem ili zakupcem. U sluaju da
        posrednik dovede u vezu sa nalogodavcem (prodavcem ili zakupodavcem) lice koje je ve
        razgledalo predmetnu nepokretnost, obavezan je da o tom upozna posrednika, a u sluaju da to ne
        uini, smatrae se da je nalogodavac sa potencijalnim kupcem ili zakupcem, prvi put doveden u
        vezu posredstvom posrednika.</p>
        <div className='subtitle'>
            <h5>II OBAVEZE POSREDNIKA</h5>
        </div>
        <p>Obaveze posrednika su:
            <ul>
                <li>- da zakljui ugovor o posredovanju sa nalogodavcem u pisanom obliku;</li>
                <li>- da nastoji da nae i dovede u vezu sa nalogodavcem lice radi zakljuenja pravnog posla;</li>
                <li>- da da nalogodavcu objektivno miljenje o ceni nepokretnosti ili iznosu zakupnine
        nepokretnosti u skladu sa njenim karakteristikama, prilikama na tritu, kao i drugim
        relevantnim okolnostima;</li>
                <li>- da izvri uvid u isprave kojima se dokazuje pravo svojine ili drugo stvarno pravo na
        nepokretnosti iji promet, odnosno zakup, je predmet posredovanja i da upozori
        nalogodavca naroito na mogue rizike u vezi sa upisom predmetne nepokretnosti u
        registre nepokretnosti, upisana prava odnosno terete na predmetnoj nepokretnosti,
        postojanje prava pree kupovine i ogranienje u pravnom prometu u skladu sa posebnim
        propisima;</li>
                <li>- da obavi potrebne radnje u cilju predstavljanja (prezentacije) nepokretnosti na tritu, da
        postavi oglas u vezi sa prometom, odnosno zakupom nepokretnosti, na odgovarajui
        nain i da izvri sve druge radnje dogovorene ugovorom o posredovanju koje prelaze
        uobiajenu prezentaciju, a za ta ima pravo na posebne, unapred iskazane trokove;</li>
                <li>- da omogui pregled nepokretnosti;</li>
                <li>- da uva podatke o linosti nalogodavca, a po pisanom nalogu nalogodavca da uva kao
        poslovnu tajnu podatke o nepokretnosti, u vezi sa ijim prometom, odnosno zakupom
        posreduje, ili u vezi sa tom nepokretnosti, ili u poslu za koji posreduje;</li>
                <li>- da obavesti nalogodavca o svim okolnostima znaajnim za predmetni posao koje su mu
        poznate;</li>
                <li> - da posreduje u pregovorima i nastoji da doe do zakljuenja ugovora;</li>
                <li>- da prisustvuje i obezbedi strunu pomo advokata vezano za izradu
        Predugovora/Ugovora o kupoprodaji nepokretnosti i ostale potrebne dokumentacije;</li>
                <li> - da prisustvuje primopredaji nepokretnosti.
        Smatra se da je posrednik omoguio nalogodavcu vezu s drugim licem (fizikim ili pravnim)
        o pregovaranju za zakljuenje pravnog posla, ako je nalogodavcu omogueno stupanje u vezu sa
        drugim licem sa kojim je pregovarao za zakljuenje pravnog posla, a posebno ako je:</li>
                <li> - neposredno odveo, ili uputio nalogodavca, ili tree lice na razgledanje predmetne
        nepokretnosti;</li>
                <li>- organizovao susret izmeu nalogodavca i treeg lica radi pregovaranja za zakljuenje
        pravnog posla;</li>
                <li> - nalogodavcu saoptio ime, broj telefona, telefaksa, ili elektronske adrese treeg lica
        zainteresovanog za zakljuenje pravnog posla, ili ako mu je saoptio tanu lokaciju
        traene nepokretnosti.</li>
            </ul>
       </p>
        <div className='subtitle'>
            <h5>III PRAVA POSREDNIKA</h5>
        </div>
        <p>Posrednik ima pravo da, uz saglasnost nalogodavca sa kojim je zakljuen Ugovor o
        posredovanju, fotografie i napravi video zapis nepokretnosti. Fotografije i video zapis posrednik
        moe koristiti iskljuivo u svrhu marketinke prezentacije predmetne nepokretnosti.
        Posrednik ima pravo da, u pisanoj odnosno elektronskoj formi, zakljui Ugovor o
        potposredovanju, kojim e u celini ili delimino, preneti svoja prava i obaveze iz Ugovora o
        posredovanju na drugog posrednika, ako se nalogodavac sa ovim izriito saglasio u Ugovoru o
        posredovanju.<br></br>
        Posrednik je duan da kopiju Ugovora o potposredovanju preda nalogodavcu u roku od pet
        dana od dana zakljuenja Ugovora o potposredovanju.
        Posrednik ima pravo da za izvrene usluge ispostavi raun nalogodavcu, u skladu sa
        zakljuenim Ugovorom o posredovanju i ovim Optim uslovima poslovanja.</p>
        <div className='subtitle'>
            <h5>IV OBAVEZE NALOGODAVCA</h5>
        </div>
        <p>Obaveze nalogodavca su:
            <ul>
                <li>- da obavesti posrednika o svim okolnostima koje su od znaaja za obavljanje
        posredovanja,</li>
                <li>- da posredniku da na uvid originalne isprave koje dokazuju njegovo pravo na
        nepokretnosti koja je predmet prometa, odnosno da upozori posrednika na sve upisane i
        neupisane terete koji postoje na nepokretnosti;</li>
                <li> - da osigura posredniku i licu zainteresovanom za zakljuenje pravnog posla razgledanje
        nepokretnosti, na dogovoreni nain i u dogovoreno vreme;</li>
                <li>- da obavesti posrednika o svim bitnim podacima o nepokretnosti, to posebno ukljuuje
        tane podatke o ceni, strukturi i dr;</li>
                <li>- da isplati posredniku ugovorenu posredniku naknadu, i ako je to posebno ugovoreno, da
        nadoknadi posredniku druge trokove nastale tokom posredovanja;</li>
                <li>
        - da obavesti posrednika pisanim putem o svim promenama u vezi sa posredovanim
        poslom, a posebno o promenama u vezi sa pravima na nepokretnosti, rokovima i cenom,
        a sve u roku od tri dana od nastale promene;</li>
                <li>- da odmah obavesti posrednika da je lice koje je preko posrednika gledalo nepokretnost
        pokazalo interesovanje da bez posredinka zakljui predugovor/ugovor o kupoprodaji
        nepokretnosti, zakupu nepokretnosti, ili obavi neki drugi pravni posao koji je posledica
        rada posrednika;</li>
                <li>- da isplati posredniku ugovorenu posredniku naknadu u celokupnom iznosu ugovorenom
        u Ugovoru o posredovanju i u sluaju kada je druga strana ili povezano lice druge strane
        takoe nalogodavac posrednika.</li>
            </ul>
        </p>
        <div className='subtitle'>
            <h5>V EKSKLUZIVNO POSREDOVANjE</h5>
        </div>
        <p>Nalogodavac se moe obavezati izriitim ugovaranjem klauzule o ekskluzivnom
        posredovanju, da u ugovorenom roku nee lino otuiti nepokretnost koja je predmet Ugovora o
        posredovanju, niti e angaovati drugog posrednika za posredovanje u vezi njenog prometa.
        Ako za vreme vaenja klauzule o ekskluzivnom posredovanju nalogodavac zakljui pravni
        posao u vezi sa predmetnom nepokretnou, za koji je posredovao drugi posrednik ili lino, bez
        posredovanja bilo kog posrednika, duan je da posredniku sa kojim je ugovorio ekskluzivno
        posredovanje na ime naknade tete isplati iznos ugovorene posrednike naknade.
        Posrednik je duan da u Ugovoru o posredovanju posebno upozori nalogodavca na znaenje
        i pravne posledice klauzule o ekskluzivnom posredovanju.</p>
        <div className='subtitle'>
            <h5>VI OSTVARIVANjE PRAVA NA POSREDNIKU NAKNADU</h5>
        </div>
        <p>Posrednik stie pravo na posredniku naknadu na dan overe predugovora/ugovora o
        kupoprodaji nepokretnosti za koju je posrednik posredovao, a najkasnije u roku od petnaest dana od
        dana overe ugovora o kupoprodaji nepokretnosti..<br></br>
        Posrednik ne moe da zahteva delimino plaanje posrednike provizije unapred, odnosno
        pre zakljuenja predugovra odnosno ugovora za koji je posredovao, sve u skladu sa prethodim
        stavom..<br></br>
        Iznos posrednike naknade, odnosno nain odreivanja iznosa posrednike naknade, kao i
        vrsta i visina trokova za dodatne usluge posrednika, utvreni su Cenovnikom posrednikih usluga
        koji je sastavni deo ovih Optih uslova poslovanja..<br></br>
        Posrednik moe da ugovori pravo na naknadu dodatnih trokova nunih za izvrenje naloga,
        bez obzira na uspeh posredovanja, i zatrai da mu se unapred plate sredstva za odreene izdatke,
        ako je to izriito navedeno u ugovoru o posredovanju..<br></br>
        Posrednik ima pravo na posredniku naknadu ako brani, odnosno vanbrani drug, potomak,
        roditelj ili bilo koje drugo povezano lice, sa kojom je posrednik doveo u vezu nalogodavca, zakljui
        posredovani pravni posao..<br></br>
        Ako nakon prestanka vaenja ugovora o posredovanju, na osnovu otkaza nalogodavca, a u
        roku ne duem od mesec dana od dana prestanka vaenja ugovora, nalogodavac zakljui pravni
        posao koji je neposredna posledica posrednikovog posredovanja pre prestanka vaenja ugovora o
        posredovanju, duan je da posredniku plati ugovorenu posredniku naknadu u celini, osim ako je
        ugovorom o posredovanju drugaije ugovoreno..<br></br>
        Ako pod uslovom i u roku iz prethodnog stava, nalogadavac zakljui pravni posao koji je u
        znaajnoj meri rezultat posrednikovog posredovanja pre prestanka vaenja ugovora o posredovanju,
        duan je da plati posredniku srazmernu posredniku naknadu, osim ako je ugovorom o
        posredovanju drugaije ugovoreno..<br></br>
        Posrednik, odnosno potposrednik nema pravo na naknadu za posredovanje ako sa
        nalogodavcem u svoje ime, kao ugovorna strana, zakljui ugovor koje je predmet posredovanja,
        odnosno ako takav ugovor sa nalogodavcem zakljui lice koje za posrednika, odnosno za
        potposredinka obavlja poslove posredovanja..<br></br>
        Trokove pribavljanja potrebne dokumentacije kojom se dokazuje svojina na predmetnog
        nepokretnosti (Izvod iz katastra i slino), a koja je potrebna za promet predmetne nepokretnosti
        snosi nalogodavac, ako nije drugaije ugovoreno u Ugovoru o posredovanju..<br></br>
        Posrednik ima pravo da odobri popust nalogodavcu na iznos posrednike naknade.</p>
        <div className='subtitle'>
            <h5>VII ODGOVORNOST ZA TETU</h5>
        </div>
        <p>Posrednik u obavljanju posredovanja, odnosno drugih radnji u vezi sa poslom koji je
        predmet posredovanja, mora postupati sa panjom dobrog privrednika..<br></br>
        Posrednik odgovara nalogodavcu u skladu sa Zakonom, za tetu koja je nastala usled
        neispunjenja ugovornih obaveza preuzetih Ugovorom o posredovanju i navedenim u ovim Optim
        uslovima poslovanja od strane posrednika..<br></br>
        Posrednik ne snosi odgovornost za izvrenje obaveza bilo koje od ugovornih strana u
        prometu, koje su meusobno preuzele u zakljuenom predugovoru/ugovoru o kupoprodaji
        nepokretnosti..<br></br>
        Posrednik ne odgovara za kvalitet nepokretnosti koja je predmet prometa, niti za skrivene
        mane (osim u sluaju da mu je prodavac u pisanoj formi saoptio da nepokretnost ima skrivenu
        manu, a on tu informaciju prikrio od kupca).</p>
        <div className='subtitle'>
            <h5>VIII ZAVRNA ODREDBA</h5>
        </div>
        <p>Posrednik u obavljanju posredovanja, odnosno drugih radnji u vezi sa poslom koji je
        predmet posredovanja, mora postupati sa panjom dobrog privrednika.
        Posrednik odgovara nalogodavcu u skladu sa Zakonom, za tetu koja je nastala usled
        neispunjenja ugovornih obaveza preuzetih Ugovorom o posredovanju i navedenim u ovim Optim
        uslovima poslovanja od strane posrednika..<br></br>
        Posrednik ne snosi odgovornost za izvrenje obaveza bilo koje od ugovornih strana u
        prometu, koje su meusobno preuzele u zakljuenom predugovoru/ugovoru o kupoprodaji
        nepokretnosti..<br></br>
        Posrednik ne odgovara za kvalitet nepokretnosti koja je predmet prometa, niti za skrivene
        mane (osim u sluaju da mu je prodavac u pisanoj formi saoptio da nepokretnost ima skrivenu
        manu, a on tu informaciju prikrio od kupca).</p>
        <div className='subtitle'>
            <h5>IX CENOVNIK USLUGA</h5>
            <Price></Price>
        </div>
    </div>
  )
}

export default Conditions
